import HighFive from "./assets/HighFive.png";
import Question1Img from "./assets/Image_68.png";
import Question2Img from "./assets/Image_69.png";

const choose = <T extends any>(list: T[], count: number) => {
    let temp = [...list];
    let output = [];

    while (count > 0 && temp.length > 0) {
        output.push(temp.splice(Math.floor(Math.random() * temp.length), 1)[0]);
        count--;
    }

    return output;
};

export const QUESTIONS = choose([
    {
        pic: Question1Img,
        prompt: "test",
        answer: "test2",
        response: "",
        gradientStart: "",
        gradientEnd: "",
    },
    {
        pic: Question2Img,
        prompt: "test",
        answer: "test2",
        response: "",
        gradientStart: "",
        gradientEnd: "",
    }
], 1);

