import React, { ChangeEventHandler, FormEventHandler, FunctionComponent, useState } from 'react';
import './App.css';
import Canvas from "./canvas";
import { INTRO_PROMPTS, QUESTIONS } from "./data";

const Component: FunctionComponent = () => {
  const [questions, setQuestions] = useState(QUESTIONS);
  const [question, setQuestion] = useState(0);
  const [introPrompt, setIntroPrompt] = useState(0);
  const [promptPosition, setPromptPosition] = useState({ x: Math.random() * 100, y: Math.random() * 100 });

  console.log(INTRO_PROMPTS);
  const onNextIntroPromptClick = () => {
    setIntroPrompt(prompt => prompt + 1);
  };

  const onResponseChange: ChangeEventHandler<HTMLTextAreaElement> = (event) => {
    setQuestions(questions => {
      let newQuestions = [...questions];
      newQuestions[question].response = event.target.value;
      return newQuestions;
    });
  };

  const onNextQuestionSubmit: FormEventHandler = (event) => {
    event.preventDefault();

    if (questions[question].response.length > 200 || questions[question].response.length === 0) return;

    setQuestion(question => question + 1);
    setPromptPosition({ x: Math.random() * 100, y: Math.random() * 100 });
  };

  return (
    <>
      {introPrompt < INTRO_PROMPTS.length ? null : <Canvas />}
      {introPrompt >= INTRO_PROMPTS.length ? null :
        <div className="intro-prompt-wrapper">
          <h1 style={{ cursor: INTRO_PROMPTS[introPrompt].cursor }} onClick={onNextIntroPromptClick}>{INTRO_PROMPTS[introPrompt].prompt}</h1>
        </div>
      }
      {(introPrompt < INTRO_PROMPTS.length || question >= QUESTIONS.length) ? null :
        <div className="question-wrapper-y">
          <div className="spacer-y" style={{ flexBasis: `${promptPosition.y}%` }} />
          <div className="question-wrapper-x">
            <div className="spacer-x" style={{ flexBasis: `${promptPosition.x}%` }} />
            <form className="question" onSubmit={onNextQuestionSubmit}>
              <h1>{questions[question].prompt}</h1>
              <img src={questions[question].pic} alt="" />
              <textarea value={questions[question].response} onChange={onResponseChange} maxLength={200} minLength={1} />
              <button type="submit">Next</button>
            </form>
          </div>
        </div>
      }
      {question < QUESTIONS.length ? null :
        <div className="response-wrapper">
          <h1>We look at the same photos and see such different things</h1>
          <div className="responses">
            {questions.map((question, ind) => <div key={ind} className="response">
              <img src={question.pic} alt="" />
              <div className="response-text-wrapper">
                <div>
                  <p>You</p>
                  <p style={{ background: `linear-gradient(${question.gradientStart} ${question.gradientEnd})` }}>{question.response}</p>
                </div>
              </div>
              <div className="answer-text-wrapper">
                <div>
                  <p>Brian</p>
                  <p>{question.answer}</p>
                </div>
              </div>
            </div>)}
          </div>
        </div>
      }
    </>
  );
};

export default Component;
